import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('diji')
export class diji extends Component {
    start() {

    }

    update(deltaTime: number) {
        let node =this.node;
        let y = node.position.y;
        y += 10*deltaTime;
        // node.setRotation(,10*deltaTime,);
    }
}


